@font-face {
  font-family: 'Oswald';
  src: url('./fonts/Oswald-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

* {
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif !important;
  -webkit-tap-highlight-color: transparent;
}

body,html {
  margin: 0;
  padding: 0;
  height: auto;
  background: linear-gradient(45deg, #ff9a9e 0%, #F4ACB7 0%);
  overflow: hidden;
  font-family: 'Oswald', sans-serif !important;

  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  min-height: 100vh;
  box-sizing: border-box;
}

.logo {
  position: absolute;
  left: 44px;
  top: 44px;
  cursor: pointer;
}

.top-button-container {
  position: absolute;
  top: 44px;
  right: calc((73 / 2560) * 100vw);
  display: flex;
  gap: calc((23 / 1440) * 100vh);
  transition: all 0.4s ease;
  z-index: 1;
}

@media (max-width: 780px) {
  .top-button-container {
    top: 4vh;
    right: 2vw;
    gap: 1vw;
    flex-direction: column;
    align-items: center;
  }

  .App {
    align-items: center;
    min-height: 94vh;
  }
}


@media screen and (max-width: 899px) and (orientation: landscape) {
  .top-button-container {
    position: absolute;
    width: 10px;
    height: 10px;
    top: 4px;
    right: 14%;
    gap: calc((23 / 1440) * 100vh);
  }
}