.toggleButton {
  width: 74px;
  height: 74px;
  background-color: #212121;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.toggleButton img {
  width: 65%;
  height: auto;
}

@media (max-width: 768px) {
  .toggleButton {
    width: calc((74 / 2560) * 100vw);
    height: calc((74 / 2560) * 100vw);
    min-width: 55.5px;
    min-height: 55.5px;
    border-radius: 15px;
  }

  .toggle-container {
    top: 30px;
  }
}

@media screen and (max-width: 899px) and (orientation: landscape) {
  .toggleButton {
    width: calc((50 / 2560) * 100vw);
    height: calc((50 / 2560) * 100vw);
    min-width: 30px;
    min-height: 30px;
    border-radius: 7px;
  }
}