.loading-container {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex; 
    align-items: center;
    justify-content: center;
    /* z-index: 10; */
}

.loading-icon {
    position: relative;
    width: 20vw;
    height: 50vh;
    top: -5%;
}

.loading-text {
    position: absolute;
    font-size: 2rem;
    color: #333;
    bottom: -30%;
}

.animated-path {
    stroke: black;
    stroke-width: 2;
    fill-opacity: 1;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    animation: draw 5s forwards ease-in-out infinite alternate, color-change 5s forwards ease-in-out infinite alternate, fill-opacity-change 5s forwards ease-in-out infinite alternate;
}

@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@keyframes color-change {
    0% {
        stroke: rgb(98, 95, 95);
    }
    100% {
        stroke: rgb(175, 121, 13);
    }
}

@keyframes fill-opacity-change {
    0% {
        fill-opacity: 0;
    }
    100% {
        fill-opacity: 1;
    }
}

.loading-text-container {
    cursor: default;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: -45vh;
    width: 100%;
    height: 100px;
    margin: auto;
    display: block;
    text-align: center;
}

.loading-text {
    position: relative;
    top: 20px;
    display: inline-block;
    animation: bounce 0.3s ease infinite alternate;
    -webkit-animation: bounce 0.3s ease infinite alternate;
    -moz-animation: bounce 0.3s ease infinite alternate;
    -o-animation: bounce 0.3s ease infinite alternate;
    font-size: 80px;
    color: #fff;
    text-shadow: 0 1px 0 #d35b5b, 0 2px 0 #d35b5b, 0 3px 0 #d35b5b, 0 4px 0 #d35b5b,
      0 5px 0 #d35b5b, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent,
      0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
}

.loading-text:nth-child(2) {
    -webkit-animation-delay: 0.1s;
    -moz-animation-delay: 0.1s;
    -o-animation-delay: 0.1s;
    animation-delay: 0.1s;
}

.loading-text:nth-child(3) {
    -webkit-animation-delay: 0.2s;
    -moz-animation-delay: 0.2s;
    -o-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

.loading-text:nth-child(4) {
    -webkit-animation-delay: 0.3s;
    -moz-animation-delay: 0.3s;
    -o-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.loading-text:nth-child(5) {
    -webkit-animation-delay: 0.4s;
    -moz-animation-delay: 0.4s;
    -o-animation-delay: 0.4s;
    animation-delay: 0.4s;
}

.loading-text:nth-child(6) {
    -webkit-animation-delay: 0.5s;
    -moz-animation-delay: 0.5s;
    -o-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

.loading-text:nth-child(7) {
    -webkit-animation-delay: 0.6s;
    -moz-animation-delay: 0.6s;
    -o-animation-delay: 0.6s;
    animation-delay: 0.6s;
}

.loading-text:nth-child(8) {
    -webkit-animation-delay: 0.7s;
    -moz-animation-delay: 0.7s;
    -o-animation-delay: 0.7s;
    animation-delay: 0.7s;
}

@-webkit-keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #d35b5b, 0 2px 0 #d35b5b, 0 3px 0 #d35b5b, 0 4px 0 #d35b5b,
        0 5px 0 #d35b5b, 0 6px 0 #d35b5b, 0 7px 0 #d35b5b, 0 8px 0 #d35b5b, 0 9px 0 #d35b5b,
        0 50px 25px rgba(0, 0, 0, 0.2);
    }
}

@-moz-keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #d35b5b, 0 2px 0 #d35b5b, 0 3px 0 #d35b5b, 0 4px 0 #d35b5b,
        0 5px 0 #d35b5b, 0 6px 0 #d35b5b, 0 7px 0 #d35b5b, 0 8px 0 #d35b5b, 0 9px 0 #d35b5b,
        0 50px 25px rgba(0, 0, 0, 0.2);
    }
}

@-o-keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #d35b5b, 0 2px 0 #d35b5b, 0 3px 0 #d35b5b, 0 4px 0 #d35b5b,
        0 5px 0 #d35b5b, 0 6px 0 #d35b5b, 0 7px 0 #d35b5b, 0 8px 0 #d35b5b, 0 9px 0 #d35b5b,
        0 50px 25px rgba(0, 0, 0, 0.2);
    }
}

@keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #d35b5b, 0 2px 0 #d35b5b, 0 3px 0 #d35b5b, 0 4px 0 #d35b5b,
        0 5px 0 #d35b5b, 0 6px 0 #d35b5b, 0 7px 0 #d35b5b, 0 8px 0 #d35b5b, 0 9px 0 #d35b5b,
        0 50px 25px rgba(0, 0, 0, 0.2);
    }
}

@media (max-width: 768px) {
    .loading-icon {
        width: 20vh;
    }
}
