.color-input {
  top: 50%;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

@media (max-width: 480px) {
  .direction-input {
    font-size: 15px !important;
  }
}

@media screen and (max-width: 899px) and (orientation: landscape) {
  .direction-input {
    font-size: 15px !important;
  }

  .color-palette {
    width: 30px;
    height: 15px;
  }

  .color-text {
    height: 15px;
  }
}