.mode-buttons-container {
  position: absolute;
  bottom: calc((100 / 1440) * 100vh);
  right: calc((73 / 2560) * 100vw);
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 746px; */
  width: 33rem;
  height: 65px;
  transition: all 0.4s ease;
  z-index: 1;
}

.background-bar {
  width: 100%;
  height: 100%;
  background-color: #212121;
  border-radius: 30px;
  position: relative;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modeButton {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 246px;
  height: 68px;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
  border-radius: 30px;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

.modeButton.active {
  background-color: #FDDB03;
  color: black;
  width: 246px;
  height: 74px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
}

@media (max-width: 768px) {
  .mode-buttons-container {
    bottom: 4vh;
    right: 10%;
    width: 80vw;
    height: 65px;
  }

  .background-bar {
    width: 100%;
    height: 75%;
    display: flex;
    justify-content: space-between;
  }

  .modeButton {
    width: 33.33%;
    font-size: 25px;
    border-radius: 0;
    margin: 0;
  }

  .modeButton.active {
    width: 33.33%;
    height: 100%;
    border-radius: 30px;
  }
}

@media (max-width: 480px) {
  .mode-buttons-container {
    bottom: 4vh;
    right: 5%;
    width: 90vw;
    height: 45px;
  }

  .background-bar {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
  }

  .modeButton {
    width: 33.33%;
    height: 100%;
    font-size: calc((40 / 768) * 100vw);
    margin: 0;
    transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
    border-radius: 30px;
  }

  .modeButton.active {
    width: 33.33%;
    height: 100%;
    border-radius: 30px;
    transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
  }
}

@media screen and (max-width: 899px) and (orientation: landscape) {
  .mode-buttons-container {
    bottom: 2vh;
    right: 3%;
    width: 40vw;
    height: 25px;
  }

  .background-bar {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
  }

  .modeButton {
    width: 33.33%;
    height: 100%;
    font-size: calc((20 / 768) * 100vw);
    margin: 0;
    transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
    border-radius: 30px;
  }

  .modeButton.active {
    width: 33.33%;
    height: 100%;
    border-radius: 30px;
    transition: background-color 0.3s, color 0.3s, border-radius 0.3s, width 0.3s, height 0.3s;
  }
}