.outlineSetting {
  display: flex;
  flex-direction: column;
  gap: calc((20/ 1440) * 100vh);
  width: 100%;
  height: 100%;
  margin-bottom: 23px;
}

.settingItem {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
}

.propertyTitle {
  flex: 0 0 25%;
  width: 25%;
  height: 54px;
  background-color: #101010;
  border-radius: 45px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
  justify-content: space-between;
}

.propertyTitleText {
  /* font-size: 24px; */
  font-size: 1rem;
  color: white;
  text-align: left;
  width: 100%;
  white-space: nowrap;
}

.outlineSettingBackground {
  flex: 1;
  height: 54px;
  background-color: #101010;
  border-radius: 45px;
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 0 15px;
  box-sizing: border-box;
}

.slider {
  width: 100%;
  height: 50%;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border-radius: 45px;
  position: relative;
  background: linear-gradient(to right, #FAAE2F 0%, #FDDD00 var(--value, 50%), #E1E1E2 var(--value, 50%), #E1E1E2 100%);
}

.slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  border-radius: 45px;
  background: linear-gradient(to right, #FAAE2F 0%, #FDDD00 var(--value, 50%), #E1E1E2 var(--value, 50%), #E1E1E2 100%);
}

.slider::-moz-range-track {
  width: 100%;
  height: 10px;
  border-radius: 45px;
  background: linear-gradient(to right, #FAAE2F 0%, #FDDD00 var(--value, 50%), #E1E1E2 var(--value, 50%), #E1E1E2 100%);
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #EBEBEB;
  border: 2px solid #FAAE2F;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -8px;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #EBEBEB;
  border: 2px solid #FAAE2F;
  border-radius: 50%;
  cursor: pointer;
}

.color-palette-container {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
  border-radius: 45px;
  overflow: hidden;
}

.options {
  width: 100%;
  height: 100%;
  background-color: #101010;
  color: white;
  border: none;
  border-radius: 45px;
  font-size: 18px;
  padding: 0 10px;
  box-sizing: border-box;
}

.modeContainer {
  display: flex;
  /* gap: 10px; */
  gap: 0.9rem;
  /* width: 100%; */
  width: 19.2rem;
  justify-content: space-between;
  align-items: center;
  padding-left: 0rem;
}

.mode {
  /* width: 90px; */
  width: 6rem;
  height: 32px;
  border: 1px solid #212121;
  border-radius: 45px;
  font-size: 15px;
  background-color: #212121;
  color: #464646;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.mode.active {
  color: #FAAE2F;
}

@media (max-width: 768px) {
  .mode {
    width: calc((70 / 768) * 100vw);
  }

  .propertyTitleText {
    /* font-size: 15px !important; */
    font-size: 1rem !important;
  }

  .colorText {
    /* font-size: 15px !important; */
    font-size: 1rem !important;
  }
}

@media (max-width: 480px) {
  .outlineSetting {
    gap: calc((10 / 1440) * 100vh);
    overflow-y: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .outlineSetting::-webkit-scrollbar {
    width: 0px;
    display: none;
  }

  .settingItem {
    flex-direction: column;
    align-items: flex-start;
    gap: 0px;
  }

  .propertyTitle {
    flex: 0 0 auto;
    width: 30%;
    height: auto;
    padding: 10px 15px;
    background-color: transparent;
  }

  .propertyTitleText {
    font-size: 18px;
  }

  .outlineSettingBackground {
    width: 100%;
    height: auto;
    padding: 10px;
  }

  .slider {
    height: 20px;
  }

  .slider::-webkit-slider-thumb,
  .slider::-moz-range-thumb {
    width: 15px;
    height: 15px;
    margin-top: -4px;
  }

  .mode {
    width: 60px;
    height: 28px;
    font-size: 12px;
  }
}

@media screen and (max-width: 899px) and (orientation: landscape) {
  .propertyTitle {
    height: 30px;
  }

  .propertyTitleText {
    font-size: 15px !important;
  }

  .outlineSettingBackground {
    height: 30px;
    width: 50%;
  }

  .slider {
    height: 30%;
  }

  .slider::-webkit-slider-thumb,
  .slider::-moz-range-thumb {
    width: 10px;
    height: 10px;
    margin-top: -4px;
  }

  .modeContainer {
    display: flex;
    gap: 10px;
    width: 50%;
    justify-content: space-between;
  }

  .mode {
    height: 15px;
  }
}