.modal {
    width: 33rem; 
    max-width: 738px;
    min-height: 500px;
    background-color: #212121;
    border: 3px solid #000000;
    border-radius: 30px;
    position: relative;
    right: calc((73 / 2560) * 100vw);
    padding: 20px;
    transition: all 0.4s ease;
    z-index: 1;
}

.toggle-container {
    width: 118px;
    height: 118px;
    min-width: 90px;
    min-height: 90px;
    background-color: #212121;
    border-radius: 50%;
    position: absolute;
    top: -15px;
    right: -15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.toggle {
    width: 86px;
    height: 86px;
    min-width: 75px;
    min-height: 75px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.toggle.off {
    background-color: #212121;
    border: 3px solid #464646;
}

.toggle.on {
    background-color: #FDDD00;
}

.toggle-text {
    font-weight: bold;
    font-size: calc((38 / 1440) * 100vh);
}

.toggle.off .toggle-text {
    color: #464646;
}

.toggle.on .toggle-text {
    color: #0C0C0C;
}

.header {
    /* width: 660px; */
    width: 30rem;
    min-width: 460px;
    background-color: #0C0C0C;
    border-radius: 30px;
    margin: 20px auto 0 auto;
    display: flex;
    align-items: center;
    padding: 1rem 0;
}

.header-text {
    font-weight: bold;
    /* font-size: calc((90/ 1440) * 100vh); */
    font-size: 3.5rem;
    color: white;
    margin-left: 24px;
}

.explain-box {
    /* width: 660px; */
    width: 30rem;
    height: calc((84/ 1440) * 100vh);
    min-width: 460px;
    background-color: #212121;
    border-radius: 45px;
    border: 3px solid #000000;
    margin: calc((39/ 1440) * 100vh) auto 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.explain-text {
    width: 90%;
    /* font-size: 20px; */
    font-size: 1rem;
    color: white;
    text-align: center;
    white-space: pre;
}

.content-box {
    /* width: 660px; */
    width: 30rem;
    height: auto;
    min-width: 460px;
    max-height: 481px;
    overflow: hidden;
    margin: calc((39/ 1440) * 100vh) auto 0 auto;
    box-sizing: border-box;
}

@media (max-width: 768px) {
    .modal {
        width: 80vw;
        height: 40vh;
        max-height: 32vh;
        bottom: 5vh;
        right: 10%;
        position: absolute;
        margin-bottom: 8vh;
        box-sizing: border-box;
        overflow: hidden;
        min-height: 370px;
        padding: 5px;
    }

    .header {
        width: 50%;
        height: 5vh;
        min-width: auto;
        margin: 20px 10px 0 10px;
        padding: 10px 0;
        text-align: center;
    }

    .header-text {
        font-size: 36px;
    }

    .explain-box {
        display: none;
    }

    .explain-text {
        font-size: 17px;
    }

    .content-box {
        width: 90%;
        height: auto;
        min-width: 370px;
        margin: calc((39 / 1440)* 100vh) 13px 0 13px;

    }

    .toggle-container {
        width: 75px;
        height: 75px;
        min-width: 75px;
        min-height: 75px;
        top: 3%;
        right: 10px;
    }

    .toggle-container .toggle {
        width: 75px;
        height: 75px;
        min-width: 75px;
        min-height: 75px;
    }
}

@media (max-width: 480px) {
    .modal {
        width: 90vw;
        min-height: 300px;
        padding: 10px;
        position: fixed;
        border: 3px solid #000000;
        border-radius: 30px;
        overflow-y: auto;
        bottom: 5vh;
        right: 5%;
        box-sizing: border-box;
        -ms-overflow-style: none;
        scrollbar-width: none;
    }

    .modal::-webkit-scrollbar {
        width: 0px;
        display: none;
    }

    .header {
        width: 30%;
        min-width: auto;
        margin: 10px 0;
        padding: 5px;
    }

    .header-text {
        font-size: 25px !important;
        text-align: center;
        margin-left: 10px;
    }

    .content-box {
        width: 100%;
        min-width: 300px;
        margin: 10px 0;
        max-height: none;
        height: auto;
        overflow: hidden;
    }

    .toggle-container {
        width: 50px;
        height: 50px;
        min-width: 50px;
        min-height: 50px;
        position: absolute;
        top: 5%;
        right: 3%;
    }

    .toggle {
        width: 50px !important;
        height: 50px !important;
        min-width: 50px !important;
        min-height: 50px !important;
    }

    .toggle-text {
        font-size: 20px;
    }

    .header-text {
        font-size: 18px;
    }

    .explain-text {
        font-size: 14px;
    }

    .content-box {
        font-size: 14px;
    }
}

